<template>
  <el-row v-loading.fullscreen.lock="loadingPage" class="report-raw-log">
    <el-col :span="24">
      <el-row type="flex" class="row-bg" justify="space-between">
        <el-col :span="24">
          <el-row type="flex" class="row-bg" justify="space-between">
            <el-col :span="12">
              <h3>WAF日志查询分析</h3>
            </el-col>
            <el-col :span="12" class="text-align-right">

            </el-col>
          </el-row>
        </el-col>
      </el-row>
      <div class="margin-4x"></div>
      <div v-if="isJxlog">
        <el-tabs v-model="tabsName" class="demo-tabs" @tab-click="handleTabsClick">
          <el-row>
            <el-col :span="24" class="search-col">
              <div class="search-form">
                <div class="search-item">
                  <div class="item-label">时间</div>
                  <div class="item-content">
                    <div class="time-select-input" v-show="timeZone!='default'">
                      <el-select v-model="timeZone" placeholder="请选择" @change="onChangeTime">
                        <el-option v-for="item in timeList" :key="item.key" :label="item.value" :value="item.key">
                        </el-option>
                      </el-select>
                    </div>
                    <div class="time-select-input" v-show="timeZone =='default'">
                      <el-date-picker v-model="pickerTime" type="datetimerange" range-separator="To" start-placeholder="开始时间"
                        end-placeholder="结束时间" />
                    </div>
                  </div>
                </div>
              </div>
            </el-col>
          </el-row>
          <el-tab-pane label="快速查询" name="simple" >
            <el-row>
              <el-col :span="24" class="search-col">
                <div class="search-form" v-for="(item, index) in searchDataList" :key="index">
                  <div class="search-item">
                    <div class="item-label">字段</div>
                    <div class="item-content">
                      <el-select v-model="item.type" placeholder="请选择">
                        <el-option label="RequestID" value="RequestID" />
                        <el-option label="SrcIP" value="SrcIP" />
                        <el-option label="Host" value="Host" />
                        <el-option label="URI" value="URI" />
                        <el-option label="UserAgent" value="UserAgent" />
                        <el-option label="QueryString" value="QueryString" />
                        <el-option label="RawBody" value="RawBody" />
                        <el-option label="RawHeaders" value="RawHeaders" />
                        <el-option label="WafModule" value="WafModule" />
                        <el-option label="WafPolicy" value="WafPolicy" />
                        <el-option label="WafAction" value="WafAction" />
                      </el-select>
                    </div>
                  </div>
                  <div class="search-item">
                    <div class="item-label">操作</div>
                    <div class="item-content">
                      <el-select v-model="item.action" placeholder="请选择">
                        <el-option label="等于" value="=" />
                        <el-option label="包含" value="like" />
                      </el-select>
                    </div>
                  </div>
                  <div class="search-item">
                    <div class="item-label">内容</div>
                    <div class="item-content">
                      <el-input placeholder="请输入" prefix-icon="el-icon-search" v-model="item.value">
                      </el-input>
                    </div>
                  </div>
            
                  <div class="search-btn">
                    <el-button @click.prevent="removeSearchItem(item)">删除</el-button>
                  </div>
                </div>
                <div class="button-form">
                  <el-button @click="addSearchItem()" plain type="primary" class="button-new">新增</el-button>
                  <el-button @click="searchItem(tabsBoxName)" type="primary" class="button-new">查询</el-button>
                </div>
              </el-col>
              <div v-show="searchMessage !=''" class="search-message">
                <p v-html="searchMessage"></p>
              </div>
            </el-row>
            <div class="margin-4x"></div>
            <el-tabs type="border-card"  v-model="tabsBoxName" @tab-click="handleTabsBoxClick()">
              <el-tab-pane label="基础展示" name="base">
                <div class="demo-block">
                  <el-table :data="tableDataSimpleBase" style=" width: 100%">
                    <el-table-column prop="RequestTime" label="RequestTime" width="150"></el-table-column>
                    <el-table-column prop="SrcIP" label="SrcIP" width="125"></el-table-column>
                    <el-table-column prop="Method" label="Method"></el-table-column>
                    <el-table-column prop="Host" label="Host"></el-table-column>
                    <el-table-column prop="URI" label="URI"></el-table-column>
                    <el-table-column prop="UserAgent" label="UserAgent"></el-table-column>
                    <el-table-column prop="Status" label="Status"></el-table-column>
                    <el-table-column prop="WafModule" label="WafModule"></el-table-column>
                    <el-table-column prop="WafPolicy" label="WafPolicy"></el-table-column>
                    <el-table-column prop="WafAction" label="WafAction"></el-table-column>
                    <el-table-column prop="RequestID" label="RequestID"></el-table-column>
                  </el-table>
                </div>
              </el-tab-pane>
              <el-tab-pane label="完整展示" name="all">
                <div class="demo-block log-table-all" >
                  <el-table :data="tableDataSimpleAll" style=" width: 100%">
                    <el-table-column prop="RequestTime" label="RequestTime" width="200"></el-table-column>
                    <el-table-column>
                      <template #default="scope">
                        <!-- <div>{{scope.row.other }}</div> -->
                        <el-input v-model="scope.row.other" autosize type="textarea" disabled/>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
              </el-tab-pane>
              <el-pagination background layout="prev, pager, next" @current-change="handleCurrentChange" :total="totalSimple"
                :page-size="20" v-model:current-page="currentPage" />
            </el-tabs>

          </el-tab-pane>
          <el-tab-pane label="高级查询" name="complex">
            <el-row>
              <el-col :span="24" class="search-col">
                <div class="search-form search-form-long">
                  <div class="search-item search-item-long">
                    <div class="item-label">内容</div>
                    <div class="item-content">
                      <el-input placeholder="请输入" prefix-icon="el-icon-search" v-model="complexValue">
                      </el-input>
                    </div>
                  </div>      
                </div>
                <div class="button-form">
                  <el-button @click="searchComplexItem(tabsBoxComplexName)" type="primary" class="button-new">查询</el-button>
                </div>
              </el-col>
              <div v-show="searchMessage !=''" class="search-message">
                <p v-html="searchMessage"></p>
              </div>
            </el-row>
            <div class="margin-4x"></div>
            <el-tabs type="border-card" v-model="tabsBoxComplexName" @tab-click="handleTabsBoxComplexClick()">
              <el-tab-pane label="基础展示" name="complex-base">
                <div class="demo-block">
                  <el-table :data="tableDataComplexBase" style=" width: 100%">
                    <el-table-column prop="RequestTime" label="RequestTime" width="150"></el-table-column>
                    <el-table-column prop="SrcIP" label="SrcIP" width="125"></el-table-column>
                    <el-table-column prop="Method" label="Method"></el-table-column>
                    <el-table-column prop="Host" label="Host"></el-table-column>
                    <el-table-column prop="URI" label="URI"></el-table-column>
                    <el-table-column prop="UserAgent" label="UserAgent"></el-table-column>
                    <el-table-column prop="Status" label="Status"></el-table-column>
                    <el-table-column prop="WafModule" label="WafModule"></el-table-column>
                    <el-table-column prop="WafPolicy" label="WafPolicy"></el-table-column>
                    <el-table-column prop="WafAction" label="WafAction"></el-table-column>
                    <el-table-column prop="RequestID" label="RequestID"></el-table-column>
                  </el-table>
            
                </div>
              </el-tab-pane>
              <el-tab-pane label="完整展示" name="complex-all">
                <div class="demo-block  log-table-all">
                  <el-table :data="tableDataComplexAll" style=" width: 100%">
                    <el-table-column prop="RequestTime" label="RequestTime" width="200"></el-table-column>
                    <el-table-column>
                      <template #default="scope">
                        <!-- <div>{{scope.row.other }}</div> -->
                        <el-input v-model="scope.row.other" autosize type="textarea" disabled />
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
              </el-tab-pane>
              <el-pagination background layout="prev, pager, next" @current-change="handleCurrentChange" :total="totalComplex"
                :page-size="20" v-model:current-page="currentComplexPage" />
            </el-tabs>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div v-else>
        <el-row>
          <el-col :span="24">
            <div class="domain-search-input">
              <el-input placeholder="请输入查询语句" prefix-icon="el-icon-search" v-model="dataSearch">
              </el-input>
            </div>
            <div class="time-select-input" v-show="timeZone!='default'">
              <el-select v-model="timeZone" placeholder="请选择" @change="onChangeTime">
                <el-option v-for="item in timeList" :key="item.key" :label="item.value" :value="item.key">
                </el-option>
              </el-select>
            </div>
            <div class="time-select-input" v-show="timeZone =='default'">
              <el-date-picker v-model="pickerTime" type="datetimerange" range-separator="To" start-placeholder="开始时间"
                end-placeholder="结束时间" />
            </div>
            <el-button type="primary" @click="onClickSearch()">查询</el-button>
          </el-col>
        
        </el-row>
        <div class="demo-block">
          <el-table :data="tableData" style=" width: 100%">
            <el-table-column prop="request_id" label="请求ID"></el-table-column>
            <el-table-column prop="request_time" label="请求时间"></el-table-column>
            <el-table-column prop="src_ip" label="源IP"></el-table-column>
            <el-table-column prop="host" label="域名"></el-table-column>
            <el-table-column prop="uri" label="访问路径"></el-table-column>
            <el-table-column prop="waf_module" label="防护模块"></el-table-column>
            <el-table-column prop="waf_extra" label="防护策略"></el-table-column>
            <el-table-column prop="waf_action" label="执行动作"></el-table-column>
          </el-table>
        </div>
      </div>
    </el-col>
  </el-row>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";

export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: true,
      dialogNameListItemFormVisible: false,
      loading: false,
      tableData: [],
      dataSearch: "",
      dataFromTime: "",
      dataToTime:"",
      timeZone: "7day",
      pickerTime: [],
      timeList: [
        { key: "7day", value: "7天" },
        { key: "24hour", value:"24小时"  },
        { key: "1hour", value: "1小时" },
        { key: "default", value: "自定义" },
      ],
      isJxlog: false,
      tabsName: "simple",
      tabsBoxName: "base",
      tabsBoxComplexName: "complex-base",
      complexValue:"1 = 1",
      tableDataSimpleBase: [],
      tableDataSimpleAll: [],
      tableDataComplexBase: [],
      tableDataComplexAll: [],
      totalSimple: 0,
      totalComplex: 0,
      currentPage: 1,
      currentComplexPage: 1,
      searchDataList: [{
        type: "",
        action: "",
        value:""
      }],
      searchMessage: "",
    };
  },
  
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      JXAjax(
        "get",
        "/waf/waf_get_sys_report_conf",
        {},
        function (response) {
          t.logSource = response.data.message.log_source;
          if (t.logSource == "") {
            t.$message({
              duration: 0,
              showClose: true,
              message: "报表配置未设置日志数据来源，" + "<a href='/#/sys-report-conf' class='error-message-btn'>" + "请前往配置" + "</a>",
              type: "warning",
              dangerouslyUseHTMLString: true,
            });
          } else { 
            t.loadingPage = false;
          }

          if (t.logSource == "ch") {
            t.isJxlog = true;
          } else { 
            t.isJxlog = false;
          }
        },
        function () {
          t.loadingPage = false;
        }
      );
    },
    onClickSearch() { 
      var t = this;
      var oData = {};
      var postUrl = "/report/cls_get_raw_log";
      if (t.logSource == "sls") { 
        postUrl = "/report/sls_get_raw_log";
      }
      if (t.logSource == "ch") {
        postUrl = "/report/ch_get_raw_log";
      }
      
      if (t.dataSearch) {
        t.getTime();
        t.onChangePicker();
        oData = {
          from_time: t.dataFromTime,
          to_time: t.dataToTime,
          sql_query: t.dataSearch
        }
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.loadingPage = false;
            if (t.logSource == "cls") {
              var _data = response.data.message.Results
              for (var i = 0; i < _data.length; i++) {
                var logJson = JSON.parse(_data[i].LogJson)
                t.tableData.push(logJson);
              }
            } else { 
              t.tableData = response.data.message
            }

          },
          function () {
            t.loadingPage = false;
          },
          "no-message"
        );
      } else { 
        t.$message({
          showClose: true,
          message: '请输入查询语句',
          type: "error",
        });
      } 
    },
    searchItem(tab) { 
      var t = this;
      var oData = {};
      var postUrl = "/report/ch_get_raw_log";
      var limit_start = 20 * (this.currentPage - 1 );
      var limit_end = 20;

      if (tab == "all") {
        postUrl = "/report/ch_get_raw_full_log";
      } else { 
        postUrl = "/report/ch_get_raw_log";
      }
      t.tableDataSimpleAll = [];
      t.tableDataSimpleBase = [];
      
      if (t.searchDataList[0].type != "" || t.searchDataList[0].action != "" || t.searchDataList[0].value != "" ) {
        t.getTime();
        t.onChangePicker();
        t.transformTime();
        oData = {
          "start_time": t.transformTime(t.dataFromTime),
          "end_time": t.transformTime(t.dataToTime),
          "sql_query_rule": JSON.stringify(t.searchDataList),
          "limit_start": limit_start.toString(),
          "limit_end": limit_end.toString(),
        }
        var dataliststr = "";
        for (var i in t.searchDataList) { 
          var item = t.searchDataList[i];
          if (item.action == "=") {
            dataliststr = dataliststr + item.type + " " + item.action + " '" + item.value + "' and ";
          } else { 
            dataliststr = dataliststr + item.type + " " + item.action + " '%" + item.value + "%' and ";
          }
        }
        dataliststr = dataliststr.slice(0, dataliststr.length - 4)
        t.searchMessage = "查询语句：select * from jxwaf.jxlog where <span class='info-red'>"  + dataliststr + "</span> and RequestTime > <span class='info-red'>'" + oData.start_time + "' </span> and RequestTime < <span class='info-red'>'" + oData.end_time + "'</span> order by RequestTime desc limit <span class='info-red'>" + oData.limit_start + "</span>,<span class='info-red'>" + oData.limit_end +"</span>";
        t.loadingPage = true;
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.loadingPage = false;
            var oData = response.data.message;
            if (response.data.totle && response.data.totle.length) { 
              t.totalSimple = response.data.totle[0][0];
            }
            if (tab == "base") { 
              if (oData && oData.length > 0) {
                oData.forEach((value) => {
                  t.tableDataSimpleBase.push({
                    // RequestTime SrcIP Method Host URI UserAgent Status WafModule WafPolicy WafAction RequestID
                    RequestTime: value[0],
                    SrcIP: value[1],
                    Method: value[2],
                    Host: value[3],
                    URI: value[4],
                    UserAgent: value[5],
                    Status: value[6],
                    WafModule: value[7],
                    WafPolicy: value[8],
                    WafAction: value[9],
                    RequestID: value[10],
                  })
                });
              }
            }

            if (tab == "all") { 
              if (oData && oData.length > 1) { 
                var data = oData[0];
                var col = oData[1];
                var index = 25;
                
                data.forEach((item) => {
                  var json = {};
                  for (var i=0; i < col.length; i++) {  
                    json[col[i][0]] = item[i]  
                    if (col[i][0] == "RequestTime") { 
                      index = i;
                    }
                  }
                  t.tableDataSimpleAll.push({
                    RequestTime: item[index],
                    other: JSON.stringify(json, null, 4),
                  })
                });
              }
            }
          },
          function () {
            t.loadingPage = false;
          },
          "no-message"
        );
      } else {
        t.$message({
          showClose: true,
          message: '请输入查询语句',
          type: "error",
        });
      } 

    },
    searchComplexItem(tab) {
      var t = this;
      var oData = {};
      var limit_start = 20 * (this.currentComplexPage - 1);
      var limit_end = 20;
      var postUrl = "/report/ch_custom_get_raw_log";
      if (tab == "complex-all") {
        postUrl = "/report/ch_custom_get_raw_full_log";
      } else {
        postUrl = "/report/ch_custom_get_raw_log";
      }
      if (t.complexValue != "") {
        t.getTime();
        t.onChangePicker();
        t.transformTime();
        oData = {
          "start_time": t.transformTime(t.dataFromTime),
          "end_time": t.transformTime(t.dataToTime),
          "custom_sql_query": t.complexValue,
          "limit_start": limit_start.toString(),
          "limit_end": limit_end.toString(),
        }
        
        t.searchMessage = "查询语句：select * from jxwaf.jxlog where <span class='info-red'>" + t.complexValue + "</span> and RequestTime > <span class='info-red'>'" + oData.start_time + "' </span> and RequestTime < <span class='info-red'>'" + oData.end_time + "'</span> order by RequestTime desc limit <span class='info-red'>" + oData.limit_start + "</span>,<span class='info-red'>" + oData.limit_end + "</span>";

        t.loadingPage = true;
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.loadingPage = false;
            var oData = response.data.message;
            if (response.data.totle && response.data.totle.length) {
              t.totalComplex = response.data.totle[0][0];
            }
            if (tab == "complex-base") {
              if (oData && oData.length > 0) {
                oData.forEach((value) => {
                  t.tableDataComplexBase.push({
                    RequestTime: value[0],
                    SrcIP: value[1],
                    Method: value[2],
                    Host: value[3],
                    URI: value[4],
                    UserAgent: value[5],
                    Status: value[6],
                    WafModule: value[7],
                    WafPolicy: value[8],
                    WafAction: value[9],
                    RequestID: value[10],
                  })
                });
              }
            }

            if (tab == "complex-all") {
              if (oData && oData.length > 1) {
                var data = oData[0];
                var col = oData[1];
                var index = 25;

                data.forEach((item) => {
                  var json = {};
                  for (var i = 0; i < col.length; i++) {
                    json[col[i][0]] = item[i]
                    if (col[i][0] == "RequestTime") {
                      index = i;
                    }
                  }
                  t.tableDataComplexAll.push({
                    RequestTime: item[index],
                    other: JSON.stringify(json, null, 4),
                  })
                });
              }
            }
            
          },
          function () {
            t.loadingPage = false;
          },
          "no-message"
        );
      } else {
        t.$message({
          showClose: true,
          message: '请输入查询语句',
          type: "error",
        });
      }

    },
    onChangeTime(value) {
      var obj = {};
      obj = this.timeList.find((item) => {
        return item.key === value;
      });
      this.timeZone = obj.key;
      
    },
    getTime() { 
      var now = Date.parse(new Date()) / 1000
      var select = "";
      if (this.timeZone == "7day") {
        select = 7 * 24 * 60 * 60
      } else if (this.timeZone == "24hour") {
        select = 24 * 60 * 60
      } else if (this.timeZone == "1hour") {
        select = 60 * 60
      } 
      this.dataFromTime = now - select;
      this.dataToTime = now;
    },
    onChangePicker() { 
      if (this.pickerTime.length > 0) { 
        this.dataFromTime = this.pickerTime[0].getTime() / 1000;
        this.dataToTime = this.pickerTime[1].getTime() / 1000;
      }
    },
    removeSearchItem(item) {
      var index = this.searchDataList.indexOf(item);
      if (index != -1 && index != 0) {
        this.searchDataList.splice(index, 1);
      }
    },
    transformTime(timestamp) { 
      var t = this;
      if (timestamp) {
        var time = new Date(timestamp * 1000);
        var y = time.getFullYear();
        var M = time.getMonth() + 1;
        var d = time.getDate();
        var h = time.getHours();
        var m = time.getMinutes();
        var s = time.getSeconds();
        return y + '-' + t.addZero(M) + '-' + t.addZero(d) + ' ' + t.addZero(h) + ':' + t.addZero(m) + ':' + t.addZero(s);
      } else {
        return '';
      }
    },
    addZero(m) {
      return m < 10 ? '0' + m : m;
    },

    addSearchItem() {
      this.searchDataList.push({ type:"", action:"", value:""})
    },
    handleTabsClick() { 
      this.currentPage = 1;
      this.searchMessage = "";
    },
    handleTabsBoxClick() { 
      var t = this;
      t.searchItem(t.tabsBoxName)
    },
    handleTabsBoxComplexClick() {
      var t = this;
      t.searchComplexItem(t.tabsBoxComplexName)
    },
    handleCurrentChange(val) { 
      var t = this;
      if (t.tabsName == 'simple') {
        t.currentPage = val;
        t.searchItem(t.tabsBoxName);
      } else { 
        t.currentComplexPage = val;
        t.searchComplexItem(t.tabsBoxComplexName);
      }
    }
  },
};
</script>
<style>
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}
.icon-success {
  color: #67c23a;
  margin-right: 5px;
}
.icon-error {
  color: #f56c6c;
  margin-right: 5px;
}
.icon-warning {
  color: #e6a23c;
  margin-right: 5px;
}
.domain-setting {
  float: right;
}
.domain-redirect {
  margin-top: 5px;
  margin-bottom: 0px !important;
}

.el-form-item.is-required .redirect-box .el-form-item__label:before {
  content: "";
}
.domain-search-input {
  display: block;
  float: left;
  text-align: left;
}
.domain-search-input .el-input {
  width: 100%;
}
.no-padding {
  padding: 0;
}
.node-detail span {
  font-size: 12px;
}
.domain-tabs {
  margin-bottom: 18px;
}
.report-raw-log .time-select-input {
  display: inline-block;
}
.report-raw-log .domain-search-input{
    width: 100%;
